.super-games {
  padding-top: 120px;
  padding-bottom: 106px;

  @include vp-1199 {
    padding: 100px 0;
  }

  @include vp-767 {
    padding: 70px 0;
  }
}

.super-games .container {
  display: grid;
  grid-template-columns: 1fr auto;

  @include vp-767 {
    grid-template-columns: 1fr;
  }
}

.super-games__content {
  color: $color-default-white;
  background-color: $color-stock-gen;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  padding-left: 76px;
  padding-top: 82px;
  padding-bottom: 85px;
  padding-right: 62px;

  @include vp-1199 {
    padding: 48px 38px 50px 44px;
    justify-content: flex-start;
  }

  @include vp-767 {
    margin-bottom: 15px;
  }
}

.super-games__info {
  display: flex;
  justify-content: space-between;
}

.super-games__date {
  display: flex;
  flex-direction: column;
}

.super-games__day {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-bottom: 19px;
  margin-right: 35px;

  @include vp-1199 {
    margin: 0;
  }
}

.super-games__time {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  margin-right: 10px;

  @include vp-1199 {
    margin: 0;
  }
}

.super-games h2 {
  font-family: $ff-alumni;
  font-weight: 600;
  font-size: 82px;
  line-height: 79px;
  letter-spacing: 0.03em;
  text-transform: uppercase;
  margin: 0;
  margin-left: 13px;
  margin-top: 5px;
  text-align: center;

  @include vp-1199 {
    font-size: 48px;
    line-height: 46px;
    margin: 0;
    margin-left: 7px;
    margin-top: 4px;
  }

  //@include vp-767 {
  //  margin-bottom: 15px;
  //}
}

.super-games p {
  font-size: 18px;
  line-height: 27px;
  margin: 0;
  margin-left: 2px;
  margin-bottom: 29px;

  @include vp-1199 {
    font-size: 12px;
    line-height: 18px;
    margin: 0;
  }

  @include vp-767 {
    margin-bottom: 36px;
  }
}

.super-games span {
  font-size: 18px;
  line-height: 27px;
  margin-bottom: 3px;

  @include vp-1199 {
    font-size: 12px;
    line-height: 18px;
    margin: 0;
  }
}

.super-games time {
  display: flex;
  align-items: center;
  font-size: 14px;
  line-height: 21px;
  position: relative;

  &::before {
    position: absolute;
    content: "";
    width: 90%;
    height: 1px;
    top: 0;
    left: 0;
    background-color: $color-default-white;
  }

  @include vp-1199 {
    font-size: 10px;
    line-height: 15px;
  }
}

.super-games b {
  font-weight: 700;
  font-size: 40px;
  line-height: 47px;
  padding-right: 7px;

  @include vp-1199 {
    font-size: 24px;
    line-height: 34px;
  }
}

.super-games__link {
  font-weight: 700;
  font-size: 12px;
  line-height: 14px;
  letter-spacing: 0.05em;
  text-transform: uppercase;
  color: $color-default-white;
  width: 347px;
  min-height: 71px;
  user-select: none;
  border: 4px solid $color-default-white;

  @include vp-1199 {
    width: 204px;
    font-size: 10px;
    line-height: 12px;
    min-height: 41px;
    margin-top: auto;
    border-width: 2px;
  }

  @include vp-767 {
    width: 100%;
  }

  @include hover-focus {
    border: 4px solid $color-text;
    outline: none;
  }

  @include active {
    color: $color-text;
  }
}

.super-games__image {
  max-width: 660px;

  @include vp-1199 {
    max-width: 386px;
  }

  @include vp-767 {
    max-width: 100%;
    height: 292px;
  }
}

.super-games__img {
  object-fit: cover;
  width: 100%;
  height: 100%;

  @include vp-1199 {
    width: 386px;
  }

  @include vp-767 {
    width: 100%;
  }
}
